import {React,useState,useEffect} from "react";
import {downArrowWhiteIcon,downArrowBlackIcon} from '../../../../../assets/icons/navbar';
import {Link} from 'react-router-dom';
import{animated,useSpring,} from 'react-spring';


function Navbar(props){

    const [toggle,setToggle]=useState(props.toggle);

    const slideUp= useSpring({
        from:{marginBottom:-800},
        to:{marginBottom:0,bottom:0}}),

        slideDown=useSpring({
            from:{bottom:-0},
            to:{bottom:-1000}});

        useEffect(()=>{
            setToggle(props.toggle)
        })

    return(
        <animated.nav  style={toggle === true? {...slideUp} : slideDown} className={`fixed  h-[70vh] border border-[1px] shadow-xl border-gray-400  flex pt-4 whitespace-nowrap overflow-x-hidden   w-full flex-col  z-50  px-4 border border-[1px] border-white space-y-4   rounded-xl bg-gray-50    `}>
            <div className="align-center bg-gray-600 w-10 h-[4px] ml-[40vw] top-0  mb-8"></div>
            <div className='flex flex-row bg-barterblue rounded-lg  space-y-2 p-4  w-50vw'>
                
                 <p className='text-font  text-white '>Browse All Categories </p>
                  <img className='h-2 w-2 ml-auto' alt='down arrow' src={downArrowWhiteIcon} />
                   </div>

                   <div className="overflow-x-hidden overflow-y-scroll  pr-4"> {props.children}</div>
        </animated.nav>
    )

}
export default Navbar