import React from 'react';

import {downArrowBlackIcon,swapIcon,wishlistIcon,profileIcon,bagIcon} from '../../../../assets/icons/navbar';
import { Navbar,Navitem } from '.';
import {useTransition,animated} from 'react-spring';

class Dropdown extends React.Component{

  constructor(props){
    super(props);
    this.state={
      isDropOpen:false,
      navitems:[
        {item:'Home',src:'',to:'/'},
        {item:'About',src:'',to:'/about'},
        {item:'Shop',src:'',to:'/shop'},
        {item:'Vendors',src:'',to:'/vendors'},
        {item:'Links',src:downArrowBlackIcon,to:'/links'},
        {item:'Contact',src:'',to:'/contact'},
        {item:'Account',src:downArrowBlackIcon,line:'true',to:'/account'},
        {item:'Swap',srcLeft:swapIcon,to:'/swap'},
        {item:'Wishlist',srcLeft:wishlistIcon,to:'/wishlist'},
        {item:'Cart',srcLeft:bagIcon,to:'/cart'},
        {item:'Profile',srcLeft:profileIcon,to:'/profile'}
      ]

    }
    
  }

  

  
  render(){
    return(
      <Navbar toggle={this.props.toggle} >
        
        {this.state.navitems.map((items)=>{
          return <Navitem item={items.item} src={items.src} srcLeft={items.srcLeft} line={items.line} to={items.to} linkToggle={this.props.linkToggle}  />
        })}
          
      </Navbar> 
    );
  }
}

export default Dropdown;