import React from "react";
import { Link } from "react-router-dom";
class Navitem extends React.Component{
    constructor(props){
        super(props)
        this.state={

        }
    }

    
    render(){
        return(
            <Link onClick={this.props.linkToggle} to={this.props.to} className={' flex text-font text-black flex-row space-y-[14px]  hover:bg-black hover:text-white w-[90vw] pb-3 pl-2  pt-1   hover:rounded-lg ' + this.renderLine()} >
                
                 {this.renderImageLeft()}
                 <p className={" text-md  mt-2 hover:bg-black hover:text-white    " }>{this.props.item}</p> 
                 {this.renderImageRight()}
            </Link>
        )
    }




    renderImageRight(){
        if(this.props.src){
         return (<img src={this.props.src} className=' h-2 w-2 ml-auto' alt='icon'/> );
        }
       
     }
 
     renderImageLeft(){
         if(this.props.srcLeft){
             return (<img src={this.props.srcLeft} className=' h-5 w-5 mr-4 mt-[17px]' alt='icon'/> );
            }
     }

     
     renderLine(){
         if(this.props.line){
            return('border-b-[1px] pb-4 border-b-gray-200');
         }
     }
     
}
export default Navitem;