import React from "react";
import {downArrowBlackIcon, searchIcon} from '../../../../assets/icons/navbar';
import {Transition,animated,useSpring} from 'react-spring'

import Dropdown from "../modal/modal";


class SearchBar extends React.Component{
  constructor(props){
    super(props);

    this.state={
      isActive:false
    }
    this.toggleDropDown=this.toggleDropDown.bind(this);
    this.renderDropDown=this.renderDropDown.bind(this);
  }

  toggleDropDown(){
    this.setState({isActive:!this.state.isActive})
  }

  renderDropDown(){
    if(this.state.isActive ){ return <Dropdown />}
  }

  

  render(){

    return(
      <div className="pt-24">
        <div className="flex flex-row my-4  border  block  border-2 border-barterblue px-4  rounded-lg ">
          <img alt='down arrow' src={downArrowBlackIcon} onClick={this.toggleDropDown} />
          <div className=' flex flex-row'>
            <div className="h-6 mt-4 ml-4 w-[1.5px] bg-gray-500"></div>
             <input className="text-fonttext focus:border-none p-4 hover:border-none" type='text' placeholder="Search for products" />
             </div>
             <img className=" mt-4 h-6 w-6 ml-auto md:ml-auto " alt='search icon' src={searchIcon}/>
             </div>
             {this.renderDropDown()}
             </div>
    );
  }

  
}

const Test=()=>{
  const fade=useSpring({
  from:{height:20},
  to:{height:400},
  leave:{height:20}
});

return(
  <animated.div className={'bg-red-200'}  style={ {
    ...fade}}>
    <Dropdown />
  </animated.div>
  
)
}

export default SearchBar;