import Home from "./windows/landing/home/home";
import Footer from "./components/forms/cards/footer/footer";
import Mobileheader from "./components/forms/cards/header/header";
import ButtomMenu from "./components/forms/cards/modal/modal";
//import {useSpring,animated} from 'react-spring'
import React from "react";
import Cart from "./windows/cart-wishlist/cart/cart";
import OrderImage from "./components/forms/cards/product/order-image";
import Shipping from "./windows/checkout/address/mobileAdress/shipping";
import Payment from "./windows/checkout/address/mobileAdress/payment";
import OrderSummary from "./windows/checkout/address/mobileAdress/order-summary";
import Address from "./windows/checkout/address/mobileAdress/adress";
import Wishlist from "./windows/cart-wishlist/wishlist/wishlist";

import {BrowserRouter as Router, Switch,Routes,Route} from 'react-router-dom';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state={
      toggleValue:false
    }
    this.toggleModal=this.toggleModal.bind(this);
    this.renderOverlay=this.renderOverlay.bind(this);
   
  }

  toggleModal(){
    this.setState({toggleValue:!(this.state.toggleValue)})
   
  }
  renderOverlay(){
    if(this.state.toggleValue){
      return  <div className="overlay h-full top-0 fixed bg-[#062a78] w-full opacity-[0.8] z-20"></div>
    } 
  }

  render(){
    return (
      <Router>
         <div className="App h-auto   overflow-y-hidden   ">
          <div className="screen-container   ">
            <div  className=" ">
              <div className='w-[100vw] md:hidden   fixed  bg-white h-auto  z-50 justify-center'>
                <Mobileheader toggle={this.toggleModal} />
                </div>
                 {this.renderOverlay()}
                  <Routes >
                     <Route path='/' element={<Home />} />
                     <Route path='/wishlist' element={<Wishlist />} />
                     <Route path='/cart' element={<Cart />} />
                  </Routes>
                <ButtomMenu  linkToggle={this.toggleModal} toggle={this.state.toggleValue} />
                </div>
             </div>
             <Footer />
             </div>
      </Router>
    );
  }
 
}

export default App;
